import store from '../store.js'
import qs from 'qs'
import axios from 'axios'
import { MessageBox,Toast,Indicator } from 'mint-ui'
import banner1 from '../assets/banner1_02.jpg'
import banner2 from '../assets/banner2_02.jpg'
import banner3 from '../assets/banner3_02.jpg'
import list1 from '../assets/list1_03.jpg'
import list2 from '../assets/list2_06.jpg'
import list3 from '../assets/list3_08.jpg'
import list4 from '../assets/list4_03.jpg'

export default {
  name: 'app',
  data () {
    return {
      appId:'',
      loadIndex:0,
      index:0,
      num:0,
      demo01_list:[{
          url: 'javascript:',
          img: banner1,
        }, {
          url: 'javascript:',
          img: banner2,
        },{
          url: 'javascript:',
          img: banner3,
        }
      ],
      listIndex:[list1,list2,list3,list4]
    }
  },
  methods: {
    numFun(){
      let that =this;
      let timer = setInterval(function(){
        that.num++;
        if(that.num >=100){
          that.num =100;
          clearInterval(timer);
          timer = null;
          setTimeout(function(){
            that.loadIndex=1;
          },1000);
        }
      },50);
    },
    toCon(e){
      store.commit('infoFun1',e);
      this.$router.push({
        name:'content'
      })

    },
    swiperFun(){
      let that = this;
      if(this.index==1){
        setTimeout(function(){
          sessionStorage.firstFlag=1;
          that.loadIndex=2;
          that.getWxconfig();

        },700);
      }
    },
    swiFun(e){
      if(e==2){
        setTimeout(function(){
          sessionStorage.firstFlag=1;
          that.loadIndex=2;
          that.getWxconfig();

        },500);
      }
    },
    getWxconfig(){
      let that =this;
      let url = encodeURIComponent(window.location.href.split('#')[0]);
      let data={
        url:url
      };
      Indicator.open('');
      this.axios({
         method: 'POST',
         url:'/api/getsign',
         headers: { 'content-type': 'application/x-www-form-urlencoded' },
         //url: '/Api/weChatShare',
         //url: '/api.php/api/Api/weChatShare?url='+url,
         data: qs.stringify({url: url})
       }).then(function (res) {
         Indicator.close();
         if(res.data.code==1){

          	sessionStorage.appId= res.data.data.appId, // 
          	sessionStorage.timestamp=res.data.data.timestamp, // 
          	sessionStorage.nonceStr= res.data.data.nonceStr, // 
          	sessionStorage.signature= res.data.data.signature,// 

            sessionStorage.shareTitle=res.data.data.title,
            sessionStorage.shareCon=res.data.data.content,
            sessionStorage.shareImg=res.data.data.image;

            // sessionStorage.appId = res.data.data.appId;
            location.href='https://open.weixin.qq.com/connect/oauth2/authorize?appid='+res.data.data.appId+'&redirect_uri='+window.location.protocol+'%2f%2f'+window.location.host+'%2fstatic%2findex.html&response_type=code&scope=snsapi_userinfo#wechat_redirect'

          }else {
            Indicator.close();
            Toast({
              message: res.data.msg,
              duration: 1500
            });
          }
       })
    },
    getUserInfo(){
      let that =this;
      Indicator.open('');
      this.axios({
         method: 'get',
         url: '/api/getuserinfo?code='+that.$route.query.code,
         //data: qs.stringify(data)
       }).then(function (res) {
         Indicator.close();
         if(res.data.code==1){
           localStorage.openid = res.data.data.openid;
           localStorage.city = res.data.data.city;
           localStorage.icon = res.data.data.icon;
           localStorage.nickname = res.data.data.nickname;
           localStorage.user_id = res.data.data.user_id;
           wx.config({
             debug: false, // ,apialertpclogpc
             appId: sessionStorage.appId, // 
             timestamp: sessionStorage.timestamp, // 
             nonceStr: sessionStorage.nonceStr, // 
             signature: sessionStorage.signature,// 
             jsApiList: [
                //'updateAppMessageShareData',
                //'updateTimelineShareData',
                'onMenuShareAppMessage',
                'onMenuShareTimeline',
                'onMenuShareQQ',,
                'onMenuShareQZone',
              ] // JS
            });
           wx.ready(function(){

             let shareUrl = window.location.protocol+'//'+window.location.host+'/static/index.html';
             //alert(shareUrl);
             wx.onMenuShareAppMessage({
                 title: sessionStorage.shareTitle, // 
                 desc: sessionStorage.shareCon, // 
                 link: shareUrl, // JS
                 imgUrl: sessionStorage.shareImg, // 
                 success: function () {
                   // 
                 }
             });
            //  wx.updateAppMessageShareData({
            //     title: sessionStorage.shareTitle, // 
            //     desc: sessionStorage.shareCon, // 
            //     link: shareUrl, // JS
            //     imgUrl: sessionStorage.shareImg, // 
            //     success: function () {
            //       // 
            //     }
            // });
            //  wx.updateTimelineShareData({
            //     title: sessionStorage.shareTitle, // 
            //     link: shareUrl, // JS
            //     imgUrl: sessionStorage.shareImg, // 
            //     success: function () {
            //       // 
            //     }
            // });
             // configreadyconfigconfigreadyready
           });
           wx.error(function(res){
             // configerrorconfigdebugresSPA
             console.log(res);
           });
          }else {
            Indicator.close();
            Toast({
              message: res.data.msg,
              duration: 1500
            });
          }
       })
    }
  },
  created:function(){
    if(sessionStorage.firstFlag==1 || this.loadIndex==2){
      this.loadIndex=2;
      if(this.$route.query.code){
        this.getUserInfo();
      }else {
        if(localStorage.openid){

        }else{
          this.getWxconfig();
        }
      }
      //
    }else {
      if(this.loadIndex==0){
        this.numFun();
      }
    }

    Indicator.close();

  }
}



