import qs from 'qs'
import axios from 'axios'
import store from '../store.js'
import { MessageBox,Toast,Indicator } from 'mint-ui'

export default {
  name: 'app',
  data () {
    return {
      isIndex:sessionStorage.isIndex,
      infoNum:sessionStorage.infoNum,
      userImg:sessionStorage.userImg,
      infoData:store.state.infoData,
      //infoData:''
    }
  },
  methods: {
    getUserInfo(){
      let that =this;
      Indicator.open('');
      this.axios({
         method: 'get',
         url: '/api/getuserinfo?code='+that.$route.query.code,
         //data: qs.stringify(data)
       }).then(function (res) {
         //alert(1);
         Indicator.close();
         if(res.data.code==1){
           localStorage.openid = res.data.data.openid;
           localStorage.city = res.data.data.city;
           localStorage.icon = res.data.data.icon;
           localStorage.nickname = res.data.data.nickname;
           localStorage.user_id = res.data.data.user_id;


           // wx.config({
           //   debug: true, // ,apialertpclogpc
           //   appId: sessionStorage.appId, // 
           //   timestamp: sessionStorage.timestamp, // 
           //   nonceStr: sessionStorage.nonceStr, // 
           //   signature: sessionStorage.signature,// 
           //   jsApiList: [
           //      //'updateAppMessageShareData',
           //      //'updateTimelineShareData',
           //      'onMenuShareAppMessage',
           //      'onMenuShareTimeline',
           //      'onMenuShareQQ',
           //      'onMenuShareQZone',
           //    ] // JS
           //  });
           // wx.ready(function(){
           //
           //   let shareUrl = window.location.protocol+'//'+window.location.host+'/static/index.html';
           //   //alert(shareUrl);
           //   wx.onMenuShareAppMessage({
           //       title: sessionStorage.shareTitle, // 
           //       desc: sessionStorage.shareCon, // 
           //       link: shareUrl, // JS
           //       imgUrl: sessionStorage.shareImg, // 
           //       success: function () {
           //         // 
           //       }
           //   });
           //
           //   // configreadyconfigconfigreadyready
           // });
           // wx.error(function(res){
           //   // configerrorconfigdebugresSPA
           //   console.log(res);
           // });

          }else {
            Indicator.close();
            Toast({
              message: res.data.msg,
              duration: 1500
            });
          }
       })
    },
    toInfo(m,n){
      let that =this;
      //console.log(this.value);
      Indicator.open('');
      sessionStorage.dream_id=m;
      this.axios({
         method: 'get',
         url: '/api/getdreambyid?openid='+localStorage.openid+'&dream_id='+m,
         //data: qs.stringify(data)
       }).then(function (res) {
         Indicator.close();
         if(res.data.code==1){
           //store.commit('infoFun2',res.data.data);
           that.infoData=res.data.data;
          }else {
            Indicator.close();
            Toast({
              message: res.data.msg,
              duration: 1500
            });
          }
       })
    }

  },
  created:function(){

    if(this.isIndex==5){
      //this.getUserInfo();
      //t
      if(this.$route.query.code){
        this.getUserInfo();
        this.toInfo(this.infoNum,1);
      }else {

        //  this.getWxconfig();

      }
    }
    //sessionStorage.id=store.state.infoData.id;
  }
}



