//const wx = require('weixin-js-sdk')
import store from '../store.js'
import qs from 'qs'
import axios from 'axios'
import { MessageBox,Toast,Indicator,TransferDomDirective as TransferDom} from 'mint-ui'

export default {
  directives: {
    TransferDom
  },
  name: 'app',
  data () {
    return {
      status:sessionStorage.status,
      txt:'',
      show2:false,
      name:localStorage.nickname,
      icon:localStorage.icon,
      deatilData:''
    }
  },
  methods: {
    xLog2(){
      this.show2=!this.show2
    },
    xLog1(){
      sessionStorage.toZan=1;
      this.$router.push({
        name:'rank'
      })
    },
    detailFu(){
      let that =this;
      Indicator.open('');
      this.axios({
         method: 'get',
         url: '/api/getdreambyuser?openid='+localStorage.openid,
         //data: qs.stringify(data)
       }).then(function (res) {
         Indicator.close();
         if(res.data.code==1){
            that.deatilData=res.data.data;
          }else {
            Indicator.close();
            Toast({
              message: res.data.msg,
              duration: 1500
            });
          }
       })
    },
    wxShare(e){
      let that = this;
      console.log(wx);
      wx.ready(function(){
        let shareUrl = window.location.protocol+'//'+window.location.host+'/static/ind.html?info='+e+'&sd='+sessionStorage.appId;
        wx.onMenuShareAppMessage({
            title: that.deatilData.title, // 
            desc: that.deatilData.detail, // 
            link: shareUrl, // JS
            imgUrl: that.deatilData.image, // 
            success: function () {
              // 
            }
        });
       //  wx.updateAppMessageShareData({
       //     title: that.deatilData.title, // 
       //     desc: that.deatilData.detail, // 
       //     link: shareUrl, // JS
       //     imgUrl: that.deatilData.image, // 
       //     success: function () {
       //       // 
       //     }
       // });
       //  wx.updateTimelineShareData({
       //     title: that.deatilData.title, // 
       //     link: shareUrl, // JS
       //     imgUrl: that.deatilData.image, // 
       //     success: function () {
       //       // 
       //     }
       // });
        // configreadyconfigconfigreadyready
      });
      wx.error(function(res){
        // configerrorconfigdebugresSPA
        console.log(res);
      });
    }
  },
  created:function(){
    this.detailFu();
    let that =this;
    setTimeout(function(){
      that.wxShare(that.deatilData.dream_id);
    },1000);

    if(this.status==1){
      this.txt =''
    }
    if(this.status==2){
      this.txt =''
    }
    if(this.status==4){
      this.txt =''
    }
    if(this.status==3){
      this.txt =''
    }

  }
}



